/*global kakao */
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import doghouse from "../images/doghouse.png";

const Container = styled.div`
  margin-top: 100px;
  display: flex;
  margin-left: 15%;
  margin-bottom: 10px;
`;
const HomeImg = styled.img`
  width: 50px;
  height: 50px;
  margin-right: 10px;
`;

const Intro = styled.div`
  font-size: x-large;
  font-weight: bolder;
  text-align: left;
  color: orange;
`;

const MapWrapper = styled.div`
  /* position: sticky; */
  margin-bottom: 100px;
  position: relative;
  width: 70%;
  height: 600px;
  margin-left: auto;
  margin-right: auto;
`;

const ListWrapper = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 350px;
  margin: 10px 0 30px 10px;
  padding: 5px;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.7);
  z-index: 1;
  font-size: 12px;
  border-radius: 10px;
  text-align: start;
`;

const Map = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  border: solid;
  border-color: orange;
  border-width: 5px;
`;

const Pagination = styled.div`
  text-align: center;
  a {
    color: black;
    font-size: 20px;
    text-decoration: none;
    margin: 0 10px;

    &.on {
      color: orange;
      font-weight: bold;
    }
  }
`;

const PlacesList = styled.ul`
  margin-top: 20px;

  li {
    list-style: none;

    &.item {
      position: relative;
      border-bottom: 1px solid #888;
      overflow: hidden;
      cursor: pointer;
      min-height: 65px;

      span {
        display: block;
        margin-top: 4px;
      }
    }
  }
`;

export default function CenterMap() {
  var keyword = " ";

  var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
  var markers = [];
  const options = {
    center: new kakao.maps.LatLng(33.450701, 126.570667),
    level: 13,
  };

  useEffect(() => {
    const container = document.getElementById("myMap");
    const map = new kakao.maps.Map(container, options);

    const ps = new kakao.maps.services.Places();

    searchPlaces();

    //    
    function searchPlaces() {
      // //      
      ps.keywordSearch(keyword, placesSearchCB);
    }

    function placesSearchCB(data, status, pagination) {
      if (status === kakao.maps.services.Status.OK) {
        let bounds = new kakao.maps.LatLngBounds();
        map.setBounds(bounds);
        displayPlaces(data);
        displayPagination(pagination);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        alert("   .");
        return;
      } else if (status === kakao.maps.services.Status.ERROR) {
        alert("    .");
        return;
      }
    }

    //      
    function displayPagination(pagination) {
      var paginationEl = document.getElementById("pagination"),
        fragment = document.createDocumentFragment(),
        i;

      //     
      while (paginationEl.hasChildNodes()) {
        paginationEl.removeChild(paginationEl.lastChild);
      }

      for (i = 1; i <= pagination.last; i++) {
        var el = document.createElement("a");
        el.href = "#";
        el.innerHTML = i;

        if (i === pagination.current) {
          el.className = "on";
        } else {
          el.onclick = (function (i) {
            return function () {
              pagination.gotoPage(i);
            };
          })(i);
        }

        fragment.appendChild(el);
      }
      paginationEl.appendChild(fragment);
    }

    //      
    function displayPlaces(places) {
      var listEl = document.getElementById("placesList"),
        menuEl = document.getElementById("menu_wrap"),
        fragment = document.createDocumentFragment(),
        bounds = new kakao.maps.LatLngBounds(),
        listStr = "";

      //      
      removeAllChildNods(listEl);

      //     
      removeMarker();

      for (var i = 0; i < places.length; i++) {
        //    
        var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
          marker = addMarker(placePosition, i),
          itemEl = getListItem(i, places[i]); //    Element 

        //       
        // LatLngBounds   
        bounds.extend(placePosition);

        //    mouseover 
        //     
        // mouseout    
        (function (marker, title) {
          kakao.maps.event.addListener(marker, "mouseover", function () {
            displayInfowindow(marker, title);
          });

          kakao.maps.event.addListener(marker, "mouseout", function () {
            infowindow.close();
          });

          itemEl.onmouseover = function () {
            displayInfowindow(marker, title);
          };

          itemEl.onmouseout = function () {
            infowindow.close();
          };
        })(marker, places[i].place_name);

        fragment.appendChild(itemEl);
      }

      //     Element 
      listEl.appendChild(fragment);
      menuEl.scrollTop = 0;

      //       
      map.setBounds(bounds);
    }

    //   Element  
    function getListItem(index, place) {
      const imgHeight = 10 + index * 46;
      var el = document.createElement("li"),
        itemStr =
          '<span class="markerbg marker_' +
          (index + 1) +
          '" style="float:left;position:absolute;width:36px; height:37px;margin:10px 0 0 10px;background:url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png) no-repeat;  background-position: 0 -' +
          imgHeight +
          'px;"></span>' +
          '<div class="info" style="padding:10px 0 10px 55px;">' +
          "   <h5>" +
          place.place_name +
          "</h5>";

      if (place.road_address_name) {
        itemStr +=
          "    <span>" +
          place.road_address_name +
          "</span>" +
          '   <span class="jibun gray" style="color:#8a8a8a; padding-left:26px;background:url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_jibun.png) no-repeat;">' +
          place.address_name +
          "</span>";
      } else {
        itemStr += "    <span>" + place.address_name + "</span>";
      }

      itemStr +=
        '  <span class="tel" style="color:#009900;">' +
        place.phone +
        "</span>" +
        "</div>";

      el.innerHTML = itemStr;
      el.className = "item";

      return el;
    }

    //       
    function removeMarker() {
      for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
      }
      markers = [];
    }

    //       
    function addMarker(position, idx, title) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
        imageSize = new kakao.maps.Size(36, 37), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(36, 691), //   
          spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
          offset: new kakao.maps.Point(13, 37), //      
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions
        ),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(map); //    
      markers.push(marker); //    

      return marker;
    }

    //        
    //   
    function displayInfowindow(marker, title) {
      var content =
        '<div style="padding:10px; width:max-content;">' + title + "</div>";

      infowindow.setContent(content);
      infowindow.open(map, marker);
    }

    //    Element  
    function removeAllChildNods(el) {
      while (el.hasChildNodes()) {
        el.removeChild(el.lastChild);
      }
    }
  }, []);

  return (
    <div>
      <Container>
        <HomeImg src={doghouse}></HomeImg>
        <Intro>  </Intro>
      </Container>

      <MapWrapper>
        <Map id="myMap"></Map>
        <ListWrapper id="menu_wrap">
          {/* <div class="option">
          <div>
            <form style={{ textAlign: "center" }} onsubmit={onSubmit}>
              <input
                type="text"
                value={searchPlace}
                id="keyword"
                size="20"
                style={{
                  width: "200px",
                  height: "30px",
                  border: "solid",
                  borderColor: "rgba(0,0,0,0.2)",
                  borderWidth: "1px",
                }}
                onChange={onInputChange}
              ></input>
              <button
                type="submit"
                style={{
                  backgroundColor: "rgb(255, 224, 166)",
                  borderColor: "rgba(0, 0, 0,0.1)",
                  height: "30px",
                }}
              >
                
              </button>
            </form>
          </div>
        </div> */}
          <PlacesList id="placesList"></PlacesList>
          <Pagination id="pagination"></Pagination>
        </ListWrapper>
      </MapWrapper>
    </div>
  );
}
