import React, { Component, useState, useRef, useEffect } from "react";
import styled from "styled-components";
import API from "./../utils/api";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";

export const EditMyInfo = () => {
  const [inputs, setInputs] = useState({
    email: "",
    nickname: "",
    password: "",
    newPassword: "",
    phoneNum: "",
  });
  const { email, nickname, password, newPassword, phoneNum } = inputs; //     

  /**    */
  const [prevProfileImg, setPrevProfileImg] = useState("");
  const default_profile_img = "https://ifh.cc/g/jLgWsT.png";
  const [image, setImage] = useState({
    image_file: "",
    preview_URL: default_profile_img,
  });
  const [defaultImg, setDefaultImg] = useState(true);
  const [isProfileChanged, setIsProfileChanged] = useState(false);
  const fileInput = useRef(null);

  /**    */
  const [isNicknameChecked, setIsNicknameChecked] = useState(false); //  

  /**    */
  const [isPasswordChecked, setIsPasswordChecked] = useState(false); //  

  /**    */
  const [prevPhoneNum, setPrevPhoneNum] = useState("");
  const [isPhonenumChecked, setIsPhonenumChecked] = useState(true); //   

  /**    */
  const OnProfileChange = (e) => {
    //   
    const reader = new FileReader();

    if (e.target.files[0]) {
      reader.readAsDataURL(e.target.files[0]);
      setDefaultImg(false);
      setIsProfileChanged(true);
    }
    reader.onload = () => {
      setImage({
        image_file: e.target.files[0],
        preview_URL: reader.result,
      });
    };
  };


  /**   */
  const checkDuplicateNickname = async () => {
    if (nickname.length === 0) {
      alert(" .");
    } else {
      try {
        const params = { nickname: nickname };
        const res = await API.get("/users/chk-nickname", { params }); // API  get   ,   res  
        console.log(res.data);
        if (res.data.isSuccess) {
          setIsNicknameChecked(true);
        } else {
          alert("  .");
        }
      } catch (e) {
        console.log(e);
      }
    }
  };

  useEffect(() => {
    API.get("/users/" + Number(sessionStorage.getItem("userID")), {
      headers: {
        "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
      },
    }).then((response) => {
      if (response.data.isSuccess) {
        const data = response.data.result;
        setPrevProfileImg(data.profileUrl);
        setPrevPhoneNum(data.phoneNum);
        console.log(data);

        setInputs({
          ...inputs,
          email: data.email,
          nickname: data.nickname,
          phoneNum: data.phoneNum,
        });
        setImage({
          image_file: "",
          preview_URL:
            data.profileUrl === null ? default_profile_img : data.profileUrl,
        });
      } else {
        console.log(response.data.result);
      }
    });
  }, []);

  const onInputChange = (e) => {
    const { value, name } = e.target; //  e.target  name  value  

    setInputs({
      ...inputs, //  input   
      [name]: value, // name    value  
    });

    if (name === "nickname") {
      setIsNicknameChecked(false);
    } else if (name === "password") {
      setIsPasswordChecked(false);
    } else if (name === "phoneNum") {
      setInputs({
        ...inputs,
        [name]: value.replace(/[^0-9]/g, "").replace(/(^02|^0505|^1[0-9]{3}|^0[0-9]{2})([0-9]+)?([0-9]{4})$/,"$1-$2-$3").replace("--", "-")
      })
    }
  };

  const onSubmitProfile = async () => {
    var deleteFlag = 0;
    if (isProfileChanged && image.image_file != "") {
      //  
      console.log("() 1");
    } else if (isProfileChanged && image.image_file === "") {
      if (prevProfileImg === null) {
        // 
        alert("  .");
        return;
      } else {
        console.log("( ) 3");
        deleteFlag = 1;
      }
    } else if (!isProfileChanged) {
      alert("  .");
      return;
    }

    try {
      const formData = new FormData();
      if (deleteFlag === 1) {
        //   
        formData.append("deleteFlag", 1);
      } else {
        formData.append("newProfileImg", image.image_file);
      }
      if (prevProfileImg != "") {
        formData.append("originProfileImgUrl", prevProfileImg);
      }

      // JSON.stringify(data)
      await API.patch("/mypage/myInfo/profileImg", formData, {
        headers: {
          "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
          "Content-Type": "multipart/form-data",
        },
      })
        .then((response) => {
          if (response.data.isSuccess) {
            alert("  .");
            window.location.reload();
          } else {
            console.log(response);
            alert("   .");
          }
        })
        .catch((error) => {
          console.log(error.response);
        });
    } catch (e) {
      console.log(e.response);
    }
  };

  const onSubmitNickName = async () => {
    if (!isNicknameChecked) {
      alert("   .");
      return;
    } else {
      try {
        // JSON.stringify(data)
        await API.patch(
          "/mypage/myInfo/nickname",
          JSON.stringify({ nickname: nickname }),
          {
            headers: {
              "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
              "Content-Type": "application/json",
            },
          }
        )
          .then((response) => {
            if (response.data.isSuccess) {
              alert(" .");
              window.location.reload();
            } else {
              console.log("", nickname);
              console.log(response);
              alert("  .");
            }
          })
          .catch((error) => {
            console.log(error.response);
          });
      } catch (e) {
        console.log(e.response);
      }
    }
  };

  const onCheckPassword = async () => {
    let data = {
      email: email,
      password: password,
    };

    try {
      // JSON.stringify(data)
      await API.post("/mypage/myInfo/checkUser", JSON.stringify(data), {
        headers: {
          "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
          "Content-Type": "application/json",
        },
      })
        .then((response) => {
          if (response.data.isSuccess) {
            alert(" .");
            setIsPasswordChecked(true);
          } else {
            console.log(response);
            alert("  .");
          }
        })
        .catch((error) => {
          console.log(error.response);
        });
    } catch (e) {
      console.log(e.response);
    }
  };

  const onSubmitPassword = async () => {
    if (!isPasswordChecked) {
      alert("    .");
      return;
    } else {
      try {
        // JSON.stringify(data)
        await API.patch(
          "/mypage/myInfo/password",
          JSON.stringify({ newPassword: newPassword }),
          {
            headers: {
              "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
              "Content-Type": "application/json",
            },
          }
        )
          .then((response) => {
            if (response.data.isSuccess) {
              alert(" .");
              window.location.reload();
            } else {
              console.log(response);
              alert("  .");
            }
          })
          .catch((error) => {
            console.log(error.response);
          });
      } catch (e) {
        console.log(e.response);
      }
    }
  };

  const onSubmitPhonenum = async () => {
    if (!isPhonenumChecked) {
      alert("  .");
      return;
    } else if (prevPhoneNum === phoneNum) {
      alert("  .");
    } else {
      try {
        // JSON.stringify(data)
        await API.patch(
          "/mypage/myInfo/phoneNum",
          JSON.stringify({ phoneNum: phoneNum }),
          {
            headers: {
              "X-ACCESS-TOKEN": sessionStorage.getItem("userJWT"),
              "Content-Type": "application/json",
            },
          }
        )
          .then((response) => {
            if (response.data.isSuccess) {
              alert(" .");
              window.location.reload();
            } else {
              console.log(response);
              alert("  .");
            }
          })
          .catch((error) => {
            console.log(error.response);
          });
      } catch (e) {
        console.log(e.response);
      }
    }
  };

  return (
    <div style={{ marginLeft: "100px" }}>
      <Title>   </Title>
      <Divider></Divider>
      <Container>
        <BoxContainer>
          <Profile>
            <ProfileText></ProfileText>
            <ProfileImageWrap>
              <ProfileImage src={image.preview_URL}></ProfileImage>
              <input
                type="file"
                style={{ display: "none" }}
                accept="image/*"
                name="profile_img"
                onChange={OnProfileChange}
                ref={fileInput}
              />
              <div style={{ display: "flex" }}>
                <SelectProfileBox
                  onClick={() => {
                    fileInput.current.click();
                  }}
                >
                   
                </SelectProfileBox>
                <SelectProfileBox> | </SelectProfileBox>
                <SelectProfileBox
                  onClick={() => {
                    setImage({
                      image_file: "",
                      preview_URL: default_profile_img,
                    });
                    setDefaultImg(true);
                    setIsProfileChanged(true);
                  }}
                >
                  
                </SelectProfileBox>
              </div>
            </ProfileImageWrap>
          </Profile>
          <EditBtn onClick={onSubmitProfile}></EditBtn>
        </BoxContainer>
        <Divider></Divider>
        <BoxContainer>
          <Box>
            <BoxText></BoxText>
            <div style={{ display: "flex", width: "300px" }}>
              <BoxSearch>
                <InputSearch
                  name="nickname"
                  onChange={onInputChange}
                  value={nickname}
                />
                <BtnSearch onClick={checkDuplicateNickname}></BtnSearch>
              </BoxSearch>
              {isNicknameChecked && (
                <CheckIcon size="2x" icon={faCheck} color="orange"></CheckIcon>
              )}
            </div>
          </Box>
          <EditBtn onClick={onSubmitNickName}></EditBtn>
        </BoxContainer>
        <Divider></Divider>
        <BoxContainer>
          <div>
            <Box style={{ marginBottom: "20px" }}>
              <BoxText> </BoxText>
              <div style={{ display: "flex", width: "300px" }}>
                <BoxSearch>
                  <InputSearch
                    name="password"
                    onChange={onInputChange}
                    value={password}
                  />
                  <BtnSearch onClick={onCheckPassword}></BtnSearch>
                </BoxSearch>
                {isPasswordChecked && (
                  <CheckIcon
                    size="2x"
                    icon={faCheck}
                    color="orange"
                  ></CheckIcon>
                )}
              </div>
            </Box>
            <Box>
              <BoxText> </BoxText>
              <div style={{ display: "flex", width: "300px" }}>
                <BoxSearch>
                  <InputSearch
                    name="newPassword"
                    onChange={onInputChange}
                    value={newPassword}
                  />
                </BoxSearch>
              </div>
            </Box>
          </div>
          <EditBtn onClick={onSubmitPassword}></EditBtn>
        </BoxContainer>
        <Divider></Divider>
        <BoxContainer>
          <Box>
            <BoxText></BoxText>
            <div style={{ display: "flex", width: "300px" }}>
              <BoxSearch>
                <InputSearch
                  name="phoneNum"
                  onChange={onInputChange}
                  value={phoneNum}
                  maxLength="13"
                />
              </BoxSearch>
            </div>
          </Box>
          <EditBtn onClick={onSubmitPhonenum}></EditBtn>
        </BoxContainer>
      </Container>
    </div>
  );
};

export default EditMyInfo;

const Title = styled.div`
  margin-top: 100px;
  font-weight: 700;
  margin-bottom: 30px;
`;

const Divider = styled.div`
  width: 100%;
  height: 1px;
  background-color: rgb(0, 0, 0, 0.1);
  margin-top: 50px;
  margin-bottom: 50px;
`;

const Container = styled.div`
  width: 800px;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 200px;
`;

const Header = styled.div`
  text-align: center;
  white-space: nowrap;
  color: orange;
  font-size: 40px;
  padding: 20px;
  font-weight: bold;
  margin-bottom: 15px;
`;

const BoxContainer = styled.div`
  white-space: nowrap;
  display: flex;
  justify-content: space-between;
`;

const BoxWrapper = styled.div`
  height: 70px;
  margin-top: 10px;
`;

const Box = styled.div`
  width: 600px;
  text-align: left;
  display: flex;
  cursor: auto;
  margin-top: auto;
  margin-bottom: auto;
`;

const BoxText = styled.div`
  width: 150px;
  text-align: left;
  display: inline-block;
  margin-right: 10px;
  font-weight: bold;
`;

const Profile = styled.div`
  width: 600px;
  text-align: left;
  display: flex;
  flex-direction: row;
  cursor: auto;
`;

const ProfileText = styled.div`
  width: 150px;
  text-align: left;
  vertical-align: top;
  margin-right: 10px;
  font-weight: bold;
`;

const SelectProfileBox = styled.div`
  width: 100px;
  text-align: left;
  vertical-align: top;
  margin-top: 5px;
  margin-right: 15px;
  padding: 5px;

  cursor: pointer;
`;

const ProfileImageWrap = styled.div`
  width: 200px;
  display: inline-block;
  text-align: left;
`;

const ProfileImage = styled.img`
  width: 100px;
  height: 100px;
  display: inline-block;
  justify-content: center;
  padding: 3px;
  border: 1px solid lightgray;
`;

const EditBtn = styled.button`
  width: 150px;
  border: none;
  background-color: orange;
  height: 60px;
  border-radius: 5px;
  position: relative;
  color: white;
  font-weight: bolder;
  margin-top: auto;
  margin-bottom: auto;
`;

const BoxSearch = styled.div`
  width: fit-content;
  height: fit-content;
  border: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.2);
  display: inline-block;
  border-radius: 3px;
`;

const InputSearch = styled.input`
  width: 200px;
  height: 40px;
  border: none;
  padding-inline: 10px;
  margin-right: 3px;
  :focus {
    outline: 2px solid gray;
  }
`;

const BtnSearch = styled.button`
  width: 100px;
  height: 42px;
  line-height: 42px;
  border: none;
  background-color: rgb(255, 224, 166);
`;

const CheckIcon = styled(FontAwesomeIcon)`
  margin-left: 10px;
  line-height: 40px;
`;
