import styled from "styled-components";
import { Link } from "react-router-dom";
import React, { Component } from "react";

const Container = styled.div`
  width: 1100px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 10px;
  padding-left: 100px;
`;

const ContainerSearch = styled.div`
  display: flex;
`;

const BoxSearch = styled.div`
  height: fit-content;
  border: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.2);
  margin-top: auto;
  margin-bottom: auto;
`;

const BoxRegion = styled.div`
  margin-top: auto;
  margin-bottom: auto;
`;

const BoxCate = styled.div`
  margin-top: auto;
  margin-bottom: auto;
`;

const BoxFilter = styled.div`
  display: ${(props) => (props.FilterVisibility ? "inline-block" : "none")};
`;

const BoxDate = styled.div`
  margin-top: 10px;
  line-height: 20px;
  display: flex;
`;

const BoxOption = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
`;

const BtnWrite = styled.button`
  border: none;
  background-color: orange;
  color: white;
  width: 150px;
  height: 70px;
  border-radius: 5%;
  visibility: ${(props) => (props.WriteVisibility ? "visible" : "hidden")};
`;

const BtnImgSearch = styled.button`
  width: 130px;
  height: 70px;
  border: none;
  background-color: orange;
  color: white;
  margin-left: 20px;
  border-radius: 5%;
  display: ${(props) => (props.ImgSearchVisibility ? "inline-block" : "none")};
`;

const InputSearch = styled.input`
  width: 450px;
  height: 50px;
  border: none;
  padding-inline: 10px;
`;

const BtnSearch = styled.button`
  width: 60px;
  height: 50px;
  border: none;
  background-color: rgb(255, 224, 166);
`;

const InputPicker = styled.select`
  width: 130px;
  height: 50px;
  margin-left: 20px;
  text-align: left;
  border-color: rgba(0, 0, 0, 0.2);
`;

const TextDate = styled.div`
  font-size: 15px;
`;

const InputDate = styled.input`
  width: 100px;
  height: 20px;
  margin-left: 10px;
  margin-right: 10px;
  border-color: rgba(0, 0, 0, 0.2);
`;

function OptionTab({
  FilterVisibility,
  WriteVisibility,
  InterestText,
  ImgSearchVisibility,
}) {
  const popUp = (event) => {
    if (sessionStorage.getItem("userID") === null) {
      alert("      .");
      event.preventDefault();
    }
  };

  return (
    <Container>
      <ContainerSearch>
        {/*  */}
        <BoxSearch>
          <InputSearch placeholder=" " />
          <BtnSearch></BtnSearch>
        </BoxSearch>
        {/*   */}
        <BoxRegion>
          <InputPicker>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
          </InputPicker>
        </BoxRegion>
        {/*   */}
        <BoxCate>
          <InputPicker>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
          </InputPicker>
        </BoxCate>
        {/*  */}
        <BoxFilter FilterVisibility={FilterVisibility}>
          <InputPicker>
            <option></option>
            <option></option>
            <option></option>
          </InputPicker>
        </BoxFilter>
        {/*  */}
        <BtnImgSearch ImgSearchVisibility={ImgSearchVisibility}>
           
        </BtnImgSearch>
      </ContainerSearch>
      {/*  */}
      <BoxDate>
        <TextDate> :</TextDate>
        <InputDate placeholder="2021.04.10" />
        <div>~</div>
        <InputDate placeholder="2022.04.10" />
      </BoxDate>
      {/* ,  */}
      <BoxOption>
        <Link to="/board/edit">
          <BtnWrite WriteVisibility={WriteVisibility} onClick={popUp}>
            
          </BtnWrite>
        </Link>
      </BoxOption>
    </Container>
  );
}

export default OptionTab;
